.navHome{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ACD9C4;
}
.navCreate{
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('https://images.wikidexcdn.net/mwuploads/wikidex/thumb/c/c8/latest/20220221171901/Artwork_aventura_en_Kanto.png/1200px-Artwork_aventura_en_Kanto.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.img{
    width: 200px;
    height: 50px;
    background-color: red;
    background: none;
}

.active{
    background-color: greenyellow;
    color: black;
    text-decoration: none;
    font-weight: bold;   
    border-radius: 100%;
    padding: 5px;
    font-size: 17px;
}
.disable{
    color: black;
    text-decoration: none;
    font-weight: bold;   
}

.inicio{
    position: absolute;
    left: 10px;
    top: 10px;
}
.button{
    color: black;
    text-decoration: none;
    font-weight: bold;
    background: none;
    border: none;
}
.home{
    margin-right: 10px;
    margin-top: 10px;
}
.create{
    margin-left: 10px;
    margin-top: 10px;
}